<template>
    <v-layout>
        <v-flex>

            <!-- Botão para adicionar nova secretaria -->
            <v-btn class="ml-2 mb-4" color="success" @click="caixaDeDialogoParaAdicionarSecretaria = true">
                <v-icon class="mr-2">mdi-clipboard-plus</v-icon>
                Secretaria
            </v-btn>

            <!-- Tabela com as secretarias -->
            <v-card>
                <v-card-title>
                Listagem das Secretarias
                <v-spacer></v-spacer>
                <v-text-field v-model="search" :append-icon="searchIcon" label="Buscar Secretaria" single-line hide-details></v-text-field>
                </v-card-title>

                <v-data-table :headers="headers" :items="secretarias" :search="search">
                    <template v-slot:item.qtdeUsers="{ item }">
                        <v-chip :color="getColor2(item.qtdeUsers)" dark>{{ item.qtdeUsers }}</v-chip>
                    </template>
                    <template v-slot:item.delete="{ item }">
                        <v-btn text @click="deletarSecretaria(item)">
                            <v-icon>{{ item.delete }}</v-icon>
                        </v-btn>
                    </template>
                    <template v-slot:item.disable="{ item }">
                        <v-icon>{{ item.disable }}</v-icon>
                    </template>
                    <template v-slot:item.edit="{ item }">
                        <v-btn text @click="openEditSecretaria(item)">
                            <v-icon>{{ item.edit }}</v-icon>
                        </v-btn>
                    </template>
                </v-data-table>
            </v-card>

            <!-- Caixa de Diálogo para adicionar nova secretaria -->
            <v-row justify="start">
                <v-dialog v-model="caixaDeDialogoParaAdicionarSecretaria" max-width="600px">
                <v-card>
                    <v-card-title>
                        <span class="headline">Nova Secretaria</span>
                    </v-card-title>
                    <v-card-text>
                        <v-container>
                            <v-row>
                                <v-col cols="12" sm="6" md="4">
                                    <v-text-field label="Sigla da Secret.*" v-model="brand" hint="Ex.: AGETEC" required></v-text-field>
                                </v-col>
                                <v-col cols="12" sm="6" md="4">
                                    <v-text-field label="Nome da Secret.*" v-model="secretary" hint="Ex.: Secretaria Municipal de Gestão" required></v-text-field>
                                </v-col>
                                <v-col cols="12" sm="6" md="4">
                                    <v-text-field label="Responsável*" v-model="resp" hint="Ex.: Paulo Fernando" required></v-text-field>
                                </v-col>
                            </v-row>
                        </v-container>
                        <small style="color: red">*Campos obrigatórios</small>
                    </v-card-text>
                    <v-card-actions>
                    <v-spacer></v-spacer>
                        <v-btn color="error" text @click="caixaDeDialogoParaAdicionarSecretaria = false">Fechar</v-btn>
                        <v-btn color="success" text @click="caixaDeDialogoParaAdicionarSecretaria = false, addSecretaria()">Salvar</v-btn>
                    </v-card-actions>
                </v-card>
                </v-dialog>
            </v-row>

            <!-- Caixa de Diálogo para editar uma secretaria já adicionada -->
            <v-row justify="start">
                <v-dialog v-model="caixaDeDialogoParaEditarSecretaria" max-width="600px">
                    <v-card>
                        <v-card-title>
                            <span class="headline">Editando a meta</span>
                        </v-card-title>
                        <v-card-text>
                            <v-container>
                                <v-row>
                                    <v-col cols="12" sm="6" md="4">
                                    <v-text-field label="Sigla da Secret.*" v-model="brandEdit" hint="Ex.: AGETEC" required></v-text-field>
                                </v-col>
                                <v-col cols="12" sm="6" md="4">
                                    <v-text-field label="Nome da Secret.*" v-model="secretaryEdit" hint="Ex.: Secretaria Municipal de Gestão" required></v-text-field>
                                </v-col>
                                <v-col cols="12" sm="6" md="4">
                                    <v-text-field label="Responsável*" v-model="respEdit" hint="Ex.: Paulo Fernando" required></v-text-field>
                                </v-col>
                                </v-row>
                            </v-container>
                            <small style="color: red">*Campos obrigatórios</small>
                        </v-card-text>
                        <v-card-actions>
                        <v-spacer></v-spacer>
                            <v-btn color="error" text @click="caixaDeDialogoParaEditarSecretaria = false">Fechar</v-btn>
                            <v-btn color="success" text @click="caixaDeDialogoParaEditarSecretaria = false, editSecretaria()">Salvar</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
            </v-row>

        </v-flex>
    </v-layout>
</template>
<script>

export default {
    
    data: () => ({
      
        headers: [
            { text: 'Abrev.', value: 'brand' },
            { text: 'Secretaria', value: 'secretary' },
            { text: 'Responsável', value: 'resp' },
            { text: 'Quant. de Usuários', value: 'qtdeUsers' },
            { text: 'Deletar', value: 'delete', align: 'center' },
            { text: 'Ativar/Desativar', value: 'disable', align: 'center' },
            { text: 'Editar', value: 'edit', align: 'center' },
        ],

        search: '',

        secretarias: [

          {
            brand: 'AGETEC',
            secretary: 'Agência Municipal de Tecnologia da Informação e Inovação',
            resp: 'Paulo Fernando Garcia Cardoso',
            qtdeUsers: 13,
            delete: 'mdi-delete',
            disable: 'mdi-check-circle',
            edit: 'mdi-account-edit'
          },
          {
            brand: 'SEGOV',
            secretary: 'Secretaria Municipal de Governo e Relações Institucionais',
            resp: 'Luciano Martins',
            qtdeUsers: 5,
            delete: 'mdi-delete',
            disable: 'mdi-check-circle',
            edit: 'mdi-account-edit'
          },
          {
            brand: 'SEGES',
            secretary: 'Secretaria Municiapl de Gestão',
            resp: 'Agenor Mattiello',
            qtdeUsers: 10,
            delete: 'mdi-delete',
            disable: 'mdi-check-circle',
            edit: 'mdi-account-edit'
          },
          {
            brand: 'CGM',
            secretary: 'Controladoria Geral do Município',
            resp: 'Luiz Afonso de Freitas Gonçalves',
            qtdeUsers: 3,
            delete: 'mdi-delete',
            disable: 'mdi-check-circle',
            edit: 'mdi-account-edit'
          }
          

        ],

        // Dados da Caixa de Diálogo para adicionar nova secretaria
        caixaDeDialogoParaAdicionarSecretaria: false,
        brand: '',
        secretary: '',
        resp: '',
        qtdeUsers: '',
        delete: '',
        disable: '',
        edit: '',

        // Dados da Caixa de Diálogo para editar uma secretaria existente
        caixaDeDialogoParaEditarSecretaria: false,
        objetoMarcadoParaEdicaoDaSecretaria: {},
        brandEdit: '',
        secretaryEdit: '',
        respEdit: '',

        // Pack de icones
        searchIcon: 'mdi-magnify',
    }),

    methods: {

        getColor2(quantidadeDeUsuarios) {
            if ( quantidadeDeUsuarios === 0 && quantidadeDeUsuarios < 0 ) return 'black'
            if ( quantidadeDeUsuarios >= 1 && quantidadeDeUsuarios <= 3 ) return 'red lighten-1'
            if ( quantidadeDeUsuarios >= 4 && quantidadeDeUsuarios <= 6 ) return 'red darken-2'
            if ( quantidadeDeUsuarios >= 7 && quantidadeDeUsuarios <= 9 ) return 'red darken-4'
            else return 'green'
        },

        addSecretaria() {
            let novaSecretaria = {}
            novaSecretaria.brand = this.brand
            novaSecretaria.secretary = this.secretary
            novaSecretaria.resp = this.resp
            novaSecretaria.qtdeUsers = 2
            novaSecretaria.delete = 'mdi-delete'
            novaSecretaria.disable = 'mdi-check-circle'
            novaSecretaria.edit = 'mdi-account-edit'
            this.secretarias.push(novaSecretaria)

        },
        
        deletarSecretaria(secretariaAserDeletada) {
            for(let i = 0; i < this.secretarias.length; i++){
                if(secretariaAserDeletada.brand === this.secretarias[i].brand){
                    this.secretarias.splice(i, 1)
                    return
                } else {
                    console.log("Secretaria não encontrada, por gentileza, contate o admin do site para informar o erro!")
                }
            }
        },

        openEditSecretaria(secretariaAserEditada) {
            this.caixaDeDialogoParaEditarSecretaria = true
            this.objetoMarcadoParaEdicaoDaSecretaria = secretariaAserEditada
            this.brandEdit = secretariaAserEditada.brand
            this.secretaryEdit = secretariaAserEditada.secretary
            this.respEdit = secretariaAserEditada.resp
        },

        editSecretaria() {
            let secretariaEditada = {}
            secretariaEditada.brand = this.brandEdit
            secretariaEditada.secretary = this.secretaryEdit
            secretariaEditada.resp = this.respEdit
            secretariaEditada.qtdeUsers = 5
            secretariaEditada.delete = 'mdi-delete'
            secretariaEditada.disable = 'mdi-check-circle'
            secretariaEditada.edit = 'mdi-account-edit'
            for(let i = 0; i < this.secretarias.length; i++){
                if(this.secretarias[i].brand === this.objetoMarcadoParaEdicaoDaSecretaria.brand){
                    this.secretarias.splice(i, 1, secretariaEditada)
                }
            }
        }


    },


}
</script>