<template>
    <v-layout>
        <v-flex>
            
            <!-- Botão para adicionar uma meta -->
            <v-btn class="ml-3 mb-4" @click="caixaDeDialogoParaAdicionarMeta = true" color="success">
                <v-icon class="mr-2">mdi-database-plus</v-icon>
                Meta
            </v-btn>

            <!-- Tabela com as Metas -->
            <v-card>
                <v-card-title>
                Listagem de todas as metas
                <v-spacer></v-spacer>
                <v-text-field v-model="search" :append-icon="searchIcon" label="Buscar Meta" single-line hide-details></v-text-field>
                </v-card-title>

                <v-data-table :headers="headers" :items="metas" :search="search">

                    <template v-slot:item.quintafase="{ item }">
                        <v-chip :color="getColor(item.quintafase)" dark>{{ item.quintafase }}</v-chip>
                    </template>
                    <template v-slot:item.delete="{ item }">
                        <v-btn text @click="deletarMeta(item)">
                            <v-icon>
                                {{ item.delete }}
                            </v-icon>
                        </v-btn>
                    </template>
                    <template v-slot:item.disable="{ item }">
                        <v-icon>{{ item.disable }}</v-icon>
                    </template>
                    <template v-slot:item.edit="{ item }">
                        <v-btn text @click="openEditMeta(item)">
                            <v-icon>{{ item.edit }}</v-icon>
                        </v-btn>

                        <!-- Caixa de Diálogo para editar uma meta já adicionada -->
                        <v-row justify="start">
                            <v-dialog v-model="caixaDeDialogoParaEditarUmaMeta" persistent max-width="600px">
                                <v-card>
                                    <v-card-title>
                                        <span class="headline">Editando a meta</span>
                                    </v-card-title>
                                    <v-card-text>
                                        <v-container>
                                            <v-row>
                                                <v-col cols="12" sm="6" md="4">
                                                    <v-text-field v-model="codeEdit" label="Código" required></v-text-field>
                                                </v-col>
                                                <v-col cols="12" sm="6" md="4">
                                                    <v-text-field v-model="secretaryEdit" label="Secretaria" hint="Ex.: AGETEC" required></v-text-field>
                                                </v-col>
                                                <v-col cols="12" sm="6" md="4">
                                                    <v-text-field v-model="respEdit" label="Responsável" hint="Ex.: Paulo Fernando" required></v-text-field>
                                                </v-col>
                                                <v-col cols="12">
                                                    <v-text-field v-model="descriptionEdit" label="Descrição" required></v-text-field>
                                                </v-col>
                                                <v-col cols="12">
                                                    <v-select :items="opcoesDosStatus" v-model="quintafaseEdit" label="Status" required></v-select>
                                                </v-col>
                                            </v-row>
                                        </v-container>
                                        <small style="color: red">*Campos obrigatórios</small>
                                    </v-card-text>
                                    <v-card-actions>
                                    <v-spacer></v-spacer>
                                        <v-btn color="error" text @click="caixaDeDialogoParaEditarUmaMeta = false">Fechar</v-btn>
                                        <v-btn color="success" text @click="caixaDeDialogoParaEditarUmaMeta = false, editMeta()">Salvar</v-btn>
                                    </v-card-actions>
                                </v-card>
                            </v-dialog>
                        </v-row>
                    </template>

                </v-data-table>
            </v-card>
            
            <!-- Caixa de Diálogo para adicionar nova meta -->
            <v-row justify="start">
                <v-dialog v-model="caixaDeDialogoParaAdicionarMeta" max-width="600px">
                <v-card>
                    <v-card-title>
                        <span class="headline">Nova Meta</span>
                    </v-card-title>
                    <v-card-text>
                        <v-container>
                            <v-row>
                                <v-col cols="12" sm="6" md="4">
                                    <v-text-field label="Código da Meta*" v-model="code" required></v-text-field>
                                </v-col>
                                <v-col cols="12" sm="6" md="4">
                                    <v-text-field label="Sigla da Secret.*" v-model="secretary" hint="Ex.: AGETEC" required></v-text-field>
                                </v-col>
                                <v-col cols="12" sm="6" md="4">
                                    <v-text-field label="Responsável*" v-model="resp" hint="Ex.: Paulo Fernando" required></v-text-field>
                                </v-col>
                                <v-col cols="12">
                                    <v-text-field label="Decrição de Meta" v-model="description" required></v-text-field>
                                </v-col>
                                <v-col cols="12">
                                    <v-select v-model="quintafase" :items="opcoesDosStatus" label="Status atual" required></v-select>
                                </v-col>
                            </v-row>
                        </v-container>
                        <small style="color: red">*Campos obrigatórios</small>
                    </v-card-text>
                    <v-card-actions>
                    <v-spacer></v-spacer>
                        <v-btn color="error" text @click="caixaDeDialogoParaAdicionarMeta = false">Fechar</v-btn>
                        <v-btn color="success" text @click="caixaDeDialogoParaAdicionarMeta = false, addMeta()">Salvar</v-btn>
                    </v-card-actions>
                </v-card>
                </v-dialog>
            </v-row>

        </v-flex>
    </v-layout>
</template>
<script>

export default {
    
    data: () => ({
      
        // Dados para a tabela com a lista de metas
        headers: [
            { text: 'Código', value: 'code' },
            { text: 'Descrição', value: 'description' },
            { text: 'Secretaria', value: 'secretary' },
            { text: 'Responsável', value: 'resp' },
            { text: 'Situação 5ª Fase', value: 'quintafase' },
            { text: 'Deletar', value: 'delete', align: 'center' },
            { text: 'Ativar/Desativar', value: 'disable', align: 'center' },
            { text: 'Editar', value: 'edit', align: 'center' },
        ],
        search: '',
        metas: [
          {
            code: 'A-01',
            description: 'VaIorizar o  servidor púbIico',
            secretary: 'SEGES',
            resp: 'Agenor Mattiello',
            quintafase: 'Ação continuada',
            delete: 'mdi-delete',
            status: 'disabled',
            disable: 'mdi-check-circle',
            edit: 'mdi-account-edit',
          },
          {
            code: 'A-02',
            description: 'Promover administração pautada na meritocracia e Gestão por projetos',
            secretary: 'SEGOV',
            resp: 'Luciano Martins',
            quintafase: 'Em andamento',
            delete: 'mdi-delete',
            status: '',
            disable: 'mdi-check-circle',
            edit: 'mdi-account-edit'
          },
          {
            code: 'A-03',
            description: 'Criar a ouvidoria do servidor',
            secretary: 'CGM',
            resp: 'Celso Costa',
            quintafase: 'Realizada',
            delete: 'mdi-delete',
            status: '',
            disable: 'mdi-check-circle',
            edit: 'mdi-account-edit'
          }
        ],

        // Dados da Caixa de Diálogo para adicionar nova meta
        caixaDeDialogoParaAdicionarMeta: false,
        statusAtualDaMeta: null,
        opcoesDosStatus: [ 'Em andamento', 'Não iniciada', 'Realizada', 'Ação continua' ],
        code: '',
        description: '',
        secretary: '',
        resp: '',
        quintafase: '',

        // Dados da Caixa de Diálogo para editar uma meta já existente
        caixaDeDialogoParaEditarUmaMeta: false,
        codeEdit: '',
        secretaryEdit: '',
        respEdit: '',
        descriptionEdit: '',
        quintafaseEdit: '',

        // Pack de icones
        searchIcon: 'mdi-magnify',

    }),

    methods: {

        getColor (estadoDaFase) {
            if (estadoDaFase === "Ação continuada") return 'blue'
            else if (estadoDaFase === "Em andamento") return 'orange'
            else if (estadoDaFase === "Realizada") return 'green'
            else if (estadoDaFase === "Não iniciada") return 'yellow'
            else return 'black'
        },

        addMeta() {
            let novaMeta = {}
            novaMeta.code = this.code
            novaMeta.description = this.description
            novaMeta.secretary = this.secretary
            novaMeta.resp = this.resp
            novaMeta.quintafase = this.quintafase
            novaMeta.delete = 'mdi-delete',
            novaMeta.disable = 'mdi-check-circle',
            novaMeta.edit = 'mdi-account-edit'
            this.metas.push(novaMeta)
        },

        deletarMeta(metaAserDeletada) {
            for(let i = 0; i < this.metas.length; i++){
                if(metaAserDeletada.code === this.metas[i].code){
                    this.metas.splice(i, 1)
                    return
                } else {
                    console.log("Meta não encontrada, por gentileza, contate o admin do site para informar o erro!")
                }
            }
        },

        openEditMeta(metaAserEditada) {
            this.caixaDeDialogoParaEditarUmaMeta = true
            this.codeEdit = metaAserEditada.code
            this.secretaryEdit = metaAserEditada.secretary
            this.respEdit = metaAserEditada.resp
            this.descriptionEdit = metaAserEditada.description
            this.quintafaseEdit = metaAserEditada.quintafase
        },

        editMeta() {
            let metaEditada = {}
            metaEditada.code = this.codeEdit
            metaEditada.secretary = this.secretaryEdit
            metaEditada.resp = this.respEdit
            metaEditada.description = this.descriptionEdit
            metaEditada.quintafase = this.quintafaseEdit
            metaEditada.delete = 'mdi-delete'
            metaEditada.disable = 'mdi-check-circle'
            metaEditada.edit = 'mdi-account-edit'
            for(let i = 0; i < this.metas.length; i++){
                if(this.metas[i].code === metaEditada.code){
                    this.metas.splice(i, 1, metaEditada)
                }
            }
        }

    }


}
</script>