<template>
    <v-layout>
        <v-flex>


            <!-- Botão para adicionar um novo usuário -->
            <v-btn class="ml-2 mb-4" color="success" @click="caixaDeDialogoParaAdicionarUser = true">
                <v-icon class="mr-2">mdi-account-plus</v-icon>
                Usuário
            </v-btn>
            
            <!-- Caixa com a tabela dos Usuários -->
            <v-card>
                <v-card-title>
                Listagem dos Usuários do Sistema
                <v-spacer></v-spacer>
                <v-text-field v-model="search" :append-icon="searchIcon" label="Buscar Usuário" single-line hide-details></v-text-field>
                </v-card-title>
                <v-data-table :headers="headers" :items="usuarios" :search="search">
                    <template v-slot:item.admin="{ item }">
                        <v-chip :color="getColor(item.admin)" dark>{{ item.admin }}</v-chip>
                    </template>
                    
                    <template v-slot:item.delete="{ item }">
                        <v-btn text @click="deletarUser(item)">
                            <v-icon>
                                {{ item.delete }}
                            </v-icon>
                        </v-btn>
                    </template>
                    
                    <template v-slot:item.disable="{ item }">
                        <v-icon>{{ item.disable }}</v-icon>
                    </template>
                    
                    <template v-slot:item.edit="{ item }">
                        <v-btn text @click="openEditUsuario(item)">
                            <v-icon>{{ item.edit }}</v-icon>
                        </v-btn>
                    </template>
                </v-data-table>
            </v-card>
            
            <!-- Caixa de Diálogo para adicionar um novo usuário -->
            <v-row justify="start">
                <v-dialog v-model="caixaDeDialogoParaAdicionarUser" max-width="600px">
                    <v-card>
                        <v-card-title>
                            <span class="headline">Novo Usuário</span>
                        </v-card-title>
                        <v-card-text>
                            <v-container>
                                <v-row>
                                    <v-col cols="12" sm="6" md="4">
                                        <v-text-field label="Secretaria" v-model="secretary" hint="Ex.: AGETEC" required></v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="6" md="4">
                                        <v-text-field label="Usuário" v-model="user" hint="Ex.: cardosodiaseduardo" required></v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="6" md="4">
                                        <v-text-field label="Nome" v-model="name" hint="Ex.: Eduardo Aparecido Dias Cardoso" required></v-text-field>
                                    </v-col>
                                    <v-col cols="12">
                                        <v-text-field label="Email" v-model="email" required></v-text-field>
                                    </v-col>
                                    <v-col cols="12">
                                        <v-text-field label="Telefone" v-model="phone" required></v-text-field>
                                    </v-col>
                                </v-row>
                            </v-container>
                            <small style="color: red">*Campos obrigatórios</small>
                        </v-card-text>
                        <v-card-actions>
                        <v-spacer></v-spacer>
                            <v-btn color="error" text @click="caixaDeDialogoParaAdicionarUser = false">Fechar</v-btn>
                            <v-btn color="success" text @click="caixaDeDialogoParaAdicionarUser = false, addUser()">Salvar</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
            </v-row>

            <!-- Caixa de Diálogo para editar um usuário existente -->
            <v-row justify="start">
                <v-dialog v-model="caixaDeDialogoParaEditarUser" max-width="600px">
                    <v-card>
                        <v-card-title>
                            <span class="headline">Editando Usuário</span>
                        </v-card-title>
                        <v-card-text>
                            <v-container>
                                <v-row>
                                    <v-col cols="12" sm="6" md="4">
                                        <v-text-field label="Secretaria" v-model="secretaryEdit" hint="Ex.: AGETEC" required></v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="6" md="4">
                                        <v-text-field label="Usuário" v-model="userEdit" hint="Ex.: cardosodiaseduardo" required></v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="6" md="4">
                                        <v-text-field label="Nome" v-model="nameEdit" hint="Ex.: Eduardo Aparecido Dias Cardoso" required></v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="10" md="8">
                                        <v-select :items="admin" label="Usuário é ADMIN do sistema?" v-model="adminEdit" required></v-select>
                                    </v-col>
                                    <v-col cols="12">
                                        <v-text-field label="Email" v-model="emailEdit" required></v-text-field>
                                    </v-col>
                                    <v-col cols="12">
                                        <v-text-field label="Telefone" v-model="phoneEdit" required></v-text-field>
                                    </v-col>
                                </v-row>
                            </v-container>
                            <small style="color: red">*Campos obrigatórios</small>
                        </v-card-text>
                        <v-card-actions>
                        <v-spacer></v-spacer>
                            <v-btn color="error" text @click="caixaDeDialogoParaEditarUser = false">Fechar</v-btn>
                            <v-btn color="success" text @click="caixaDeDialogoParaEditarUser = false, editUser()">Salvar</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
            </v-row>


        </v-flex>
    </v-layout>
</template>

<script>

export default {
    
    data: () => ({
      
        headers: [
            { text: 'Secretaria', value: 'secretary' },
            { text: 'Usuário', value: 'user' },
            { text: 'Nome', value: 'name' },
            { text: 'Email', value: 'email' },
            { text: 'Telefone(Ramal)', value: 'phone', align: 'center' },
            { text: 'Admin (sim ou não)', value: 'admin', align: 'center' },
            { text: 'Deletar', value: 'delete', align: 'center' },
            { text: 'Ativar/Desativar', value: 'disable', align: 'center' },
            { text: 'Editar', value: 'edit', align: 'center' },
        ],

        search: '',

        usuarios: [

          {
            secretary: 'AGETEC',
            user: 'cardosodiaseduardo',
            name: 'Eduardo Aparecido Dias Cardoso',
            email: 'eduardo.cardoso@agetec.campogrande.ms.gov.br',
            phone: 67333145518,
            admin: "SIM",
            delete: 'mdi-delete',
            disable: 'mdi-check-circle',
            edit: 'mdi-account-edit'
          },
          {
            secretary: 'AGETEC',
            user: 'williamyugo',
            name: 'William Yugo Kokubo',
            email: 'william.kokubo@agetec.campogrande.ms.gov.br',
            phone: 67333145518,
            admin: "SIM",
            delete: 'mdi-delete',
            disable: 'mdi-check-circle',
            edit: 'mdi-account-edit'
          },
          {
            secretary: 'AGETEC',
            user: 'dondimatos',
            name: 'Claudio Ramos',
            email: 'claudio.ramos@agetec.campogrande.ms.gov.br',
            phone: 67333145518,
            admin: "NÃO",
            delete: 'mdi-delete',
            disable: 'mdi-check-circle',
            edit: 'mdi-account-edit'
          },
          {
            secretary: 'AGETEC',
            user: 'cardosopaulofernando',
            name: 'Paulo Fernando Garcia Cardoso',
            email: 'diretor.presidente@agetec.campogrande.ms.gov.br',
            phone: 67333145518,
            admin: "NÃO",
            delete: 'mdi-delete',
            disable: 'mdi-check-circle',
            edit: 'mdi-account-edit'
          },
          {
            secretary: 'SEGOV',
            user: 'martinsluciano67',
            name: 'Luciano Martins',
            email: 'advlucianomartins@gmail.com',
            phone: 67333145518,
            admin: "NÃO",
            delete: 'mdi-delete',
            disable: 'mdi-check-circle',
            edit: 'mdi-account-edit'
          },
          {
            secretary: 'GAPRE',
            user: 'marquinhostrad',
            name: 'Marcos Marcello Trad',
            email: 'gabinete@campogrande.ms.gov.br',
            phone: 67333145518,
            admin: "NÃO",
            delete: 'mdi-delete',
            disable: 'mdi-check-circle',
            edit: 'mdi-account-edit'
          }

        ],

        // Dados da Caixa de Diálogo para adicionar um novo usuário
        caixaDeDialogoParaAdicionarUser: false,
        secretary: '',
        user: '',
        name: '',
        email: '',
        phone: '',

        // Dados da Caixa de Diálogo para editar um novo usuário
        caixaDeDialogoParaEditarUser: false,
        objetoMarcadoParaEdicaoDoUsuario: {},
        secretaryEdit: '',
        userEdit: '',
        adminEdit: '',
        admin: [ "SIM", "NÃO"],
        nameEdit: '',
        emailEdit: '',
        phoneEdit: '',

        // Pack de Icons
        searchIcon: 'mdi-magnify',


    }),

    methods: {

        getColor (ehAdminOuNao) {
            if (ehAdminOuNao === "NÃO") return 'red'
            else return 'green'
        },

        addUser() {
            let novoUser = {}
            novoUser.secretary = this.secretary
            novoUser.user = this.user
            novoUser.name = this.name
            novoUser.email = this.email
            novoUser.phone = this.phone
            novoUser.admin = "NÃO"
            novoUser.delete = 'mdi-delete',
            novoUser.disable = 'mdi-check-circle',
            novoUser.edit = 'mdi-account-edit'
            this.usuarios.push(novoUser)
        },

        deletarUser(userAserDeletado) {
            for(let i = 0; i < this.usuarios.length; i++){
                if(userAserDeletado.user === this.usuarios[i].user){
                    this.usuarios.splice(i, 1)
                    return
                } else {
                    console.log("Usuário não encontrado, por gentileza, contate o admin do site para informar o erro!")
                }
            }
        },

        openEditUsuario(userAserEditado) {
            this.caixaDeDialogoParaEditarUser = true
            this.objetoMarcadoParaEdicaoDoUsuario = userAserEditado
            this.secretaryEdit = userAserEditado.secretary
            this.userEdit = userAserEditado.user
            this.adminEdit = userAserEditado.admin
            this.nameEdit = userAserEditado.name
            this.emailEdit = userAserEditado.email
            this.phoneEdit = userAserEditado.phone
        },
        
        editUser() {
            let userEditado = {}
            userEditado.secretary = this.secretaryEdit
            userEditado.user = this.userEdit
            userEditado.name = this.nameEdit
            userEditado.email = this.emailEdit
            userEditado.phone = this.phoneEdit
            userEditado.admin = this.adminEdit
            userEditado.delete = 'mdi-delete'
            userEditado.disable = 'mdi-check-circle'
            userEditado.edit = 'mdi-account-edit'
            for(let i = 0; i < this.usuarios.length; i++){
                if(this.usuarios[i].user === this.objetoMarcadoParaEdicaoDoUsuario.user){
                    this.usuarios.splice(i, 1, userEditado)
                }
            }
        }

    },

}
</script>