import Vue from 'vue'
import VueRouter from 'vue-router'
import Vuetify from 'vuetify'
import Home from '../views/Home.vue'
import About from '../views/About.vue'
import Usuarios from '../views/Usuarios.vue'
import Secretarias from '../views/Secretarias.vue'
import Metas from '../views/Metas.vue'
import MetasTecnico from '../views/MetasTecnico.vue'

Vue.use(VueRouter)
Vue.use(Vuetify)
const routes = [
  {
    path: '/',
    name: 'home',
    component: Home
  },
  {
    path: '/about',
    name: 'about',
    component: About
  },
  {
    path: '/usuarios',
    name: 'usuarios',
    component: Usuarios
  },
  {
    path: '/secretarias',
    name: 'secretarias',
    component: Secretarias
  },
  {
    path: '/metas',
    name: 'metas',
    component: Metas
  },
  {
    path: '/metasTecnico',
    name: 'metasTecnico',
    component: MetasTecnico
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
